import React, { Component } from 'react';

class Header extends Component {
  // const [displayProduct, setDisplayProduct] = useState(false);
  // const [displayKnowMore, setDisplayKnowMore] = useState(false);
  // const [toogleNav, setToogleNav] = useState(true);
  // const [toogleSearch, settoogleSearch] = useState(true);

  // handleToggle = value => {
  //   if (value === 'displayProduct') {
  //     setDisplayProduct(!displayProduct)
  //     displayKnowMore === true && setDisplayKnowMore(!displayKnowMore)
  //   }
  //   if (value === 'displayKnowMore') {
  //     setDisplayKnowMore(!displayKnowMore)
  //     displayProduct === true && setDisplayProduct(!displayProduct)
  //   }
  // }

  constructor(props) {
    super(props);
    this.state = {
      displayProduct : false,
      displayKnowMore : false,
      toogleNav : true,
      toogleSearch : true
    }
  }
  componentDidUpdate() {
    document.addEventListener('click', this.handleDocumentClick, false)
    document.addEventListener('touchend', this.handleDocumentClick, false)
  }
  componentWillUnmount() {
    document.removeEventListener('click', this.handleDocumentClick, false)
    document.removeEventListener('touchend', this.handleDocumentClick, false)
  }

  handleDocumentClick = e => {
    if (this) {
      if (!findDOMNode(this).contains(e.target)) {
        this.setState({ toogleNav : true })
      }
    }
  }

  handleToggle = value => {
    const { displayProduct, displayKnowMore } = this.state;

    if (value === 'displayProduct') {
      // setDisplayProduct(!displayProduct)
      this.setState({ displayProduct: !displayProduct})
      displayKnowMore === true && this.setState({ displayKnowMore : !displayKnowMore })
    }
    if (value === 'displayKnowMore') {
      this.setState({ displayKnowMore : !displayKnowMore })
      displayProduct === true && this.setState({ displayProduct: !displayProduct})
    }
  }

  render() {
    const { handleToggle } = this;
    const { styleColorTextHeader } = this.props;
    const { displayProduct, displayKnowMore, toogleNav, toogleSearch } = this.state;

    const mainStyle = css`
    @import url('https://fonts.googleapis.com/css?family=Ubuntu:400,400i,700,700i');
    font-family: 'Ubuntu', sans-serif;
    position: fixed;
    z-index: 1;
    box-shadow: 0px -9px 25px 0px rgba(0,0,0,0.8);
    background-color: #fff;
    width: 100%;
    margin: 0px;
    a {
      text-decoration: none;
    }
    .hamburger {
      cursor: pointer;
      padding-top: 5px;
      width: 10%;
      float: right;
      transition: 0.5s;
      div {
        background-color: #000;
        height: 2px;
        margin: 6px 0px;
        transition: 0.5s;
      }
    }
    .cross {
      div:nth-child(1) {
        transform: rotate(-45deg);
        margin-top: 13px;
        transition: 0.5s;
      }
      div:nth-child(2) {
        transform: rotate(45deg);
        margin-top: -6px;
        transition: 0.5s;
      }
      div:nth-child(3) {
        transform: rotate(45deg);
        margin-top: -6px;
        transition: 0.5s;
      }
      width: 10%;
      float: right;
      div {
        background-color: #000;
        height: 2px;
        margin: 4px 0px;
      }
      transition: 0.5s;
    }
    .main-menu {
      overflow: scroll;
    }
    ul {
      float: right;
      list-style-type: none;
      margin: 0;
      padding: 0;
      overflow: hidden;
      li {
        float: left;
        margin-top: 10px;
        a {
          cursor: pointer;
          text-decoration: none;
          padding: 14px;
          ${styleColorTextHeader ||`color: #000 `};
          font-size: 0.9rem;
          span img {
            margin-bottom: -6px;
          }
        }
      }
    }
    .sub-menu{
      overflow: inherit;
      margin: -30px 0px -30px -14px;
      height: 110px;
      padding: 10px 0px;
      li {
        width: 25%;
        a {
          padding: 10px;
          span{
            margin-right:10px;
          }
        }
      }
    }
    .blogs-link {
      border-left: 1px solid lightgrey;
      position: absolute;
      font-size: 2rem;
      margin-left: 7px;
      padding-left: 10px;
      color: #1798D1;
    }
    .responsive {
      display: block;
      transition: all 0.5s linear;
    }
    .hamburger, .cross {
      display: none;
    }
    @media screen and (max-width: 600px) {
      margin: 0px;
      ul {
        li {
          margin: 15px 0px;
          width: 100%;
        }
      }
      .sub-menu {
        height: 200px;
        li {
          text-align: center;
          width: 50%;
          a {
            padding: 0px;
          }
        }
      }
      .responsive {
        display: none;
        transition: all 0.5s linear;
      }
      .hamburger, .cross {
        display: block;
      }
    }
    .search-section {
      float: right;
    }
    .hidden-search {
      display: none;
    }
    .close-icon {
      position: absolute;
      cursor: pointer;
      font-size: 31px;
      margin: -14px 0px 0px 21px;
    }
    .sub-menu-responsive {
      display: none;
    }
    @media screen and (max-width: 600px) {
      .sub-menu-responsive {
        display: block;
        ul {
          li {
            width: 50%;
            font-size: 0.6rem;
            text-align: center;
          }
        }
      }
      .display-important-600 {
        display: none;
      }
      .main-menu {
        float: left;
      }
      .close-icon {
        display: none;
      }
      .search-button {
        display: none;
      }
      .hidden-search {
        display: block;
      }
    }
  `
  const mainGridStyle = css`
    width: 60%;
    @media screen and (max-width: 600px) {
      width: 90%;
    }
  `
  const gridMenuStyle = css`
    margin-bottom: 20px;
  `
  const gridListStyle = css`
    background-color: white;
    box-shadow: 0px 3px 6px -2px rgba(0,0,0,0.8);
    padding: 24px 10px;
    position: absolute;
    top: 75px;
    right: 231px;
    @media screen and (max-width: 600px) {
      padding: 0px;
      left: 0;
      top: 192px;
    }
  `
  const mainGridStyleSubscriber = css`
    label: nama;
    height: 0;
  `
  const inputStyleSearch = css`
    font-family: 'Ubuntu',sans-serif;
    font-size: 1rem;
    width: 60%;
    position: absolute;
    right: 20%;
    top: 18px;
    @media screen and (max-width: 600px) {
      right: unset;
      top: 72px;
      width: 83%;
      margin: 0px;
      margin-top: 0px;
      border: 1px solid lightgrey;
      outline: none;
    }
  `

  return (
    <div className={ mainStyle }>
    <MainGrid style={ mainGridStyle }>
      <ListGrid cols={8} style={ gridMenuStyle }>
        <div>
          <div className={ toogleNav ? 'hamburger' : 'cross' } onClick={ () => this.setState({ toogleNav : !toogleNav })}>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
        <Link href="/">
          <a>
            <img src="/static/docob_logo.svg" alt="docoblast-logo" width={100}/>
          </a>
        </Link>
        <Link href="/blogs">
          <a className="blogs-link">
            <span>Blog</span>
          </a>
        </Link>
      </ListGrid>
      <div className={ toogleNav ? 'responsive' : '' }>
        <ListGrid cols={16}>
          <ul className="main-menu">
            <li className="search-section">
              <div className={ toogleSearch && 'hidden-search' }>
                <Input placeholder="Masukkan Pencarian" style={ inputStyleSearch } />
                <span class="close-icon" onClick={ () => this.setState({ toogleSearch : !toogleSearch })}>&times;</span>
              </div>
              <a className="search-button" onClick={ () => this.setState({ toogleSearch : !toogleSearch })}>
                <img src="/static/ic_search.svg" alt="ic-search" width={20}/>
              </a>
            </li>
            <li>
              <a onClick={() => handleToggle('displayProduct')}>
                <span>Kategori</span>
                <span>
                  <img src={ displayProduct ? "/static/ic_up.svg" : "/static/ic_down.svg" } alt="icons" width={20}/>
                </span>
              </a>
              <div className="sub-menu-responsive">
                <ul className={ displayProduct ? css`display:block;` : css`display:none;` }>
                  <li><Link href="/products"><a>DocoBlast</a></Link></li>
                  <li><Link href="/products/why"><a>Mengapa DocoBlast?</a></Link></li>
                  <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
                  <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
                  <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
                  <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
                  <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
                  <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
                  <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
                </ul>
              </div>
            </li>
            <li>
              <a onClick={ () => handleToggle('displayKnowMore') }>
                <span>Berlangganan</span>
                <span>
                  <img src={ displayKnowMore ? "/static/ic_up.svg" : "/static/ic_down.svg" } alt="icons" width={20}/>
                </span>
              </a>
            </li>
          </ul>
        </ListGrid>
      </div>
    </MainGrid>
    <div className={ toogleNav ? 'responsive' : '' }>
      <div className="display-important-600">
        <MainGrid style={ mainGridStyle } display={displayProduct ? 'block' : 'none'}>
          <ListGrid cols={24}>
            <ul className="sub-menu">
              <li><Link href="/products"><a>DocoBlast</a></Link></li>
              <li><Link href="/products/why"><a>Mengapa DocoBlast?</a></Link></li>
              <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
              <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
              <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
              <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
              <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
              <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
              <li><Link href="/products/benefit"><a>Manfaat</a></Link></li>
            </ul>
          </ListGrid>
        </MainGrid>
      </div>
      <MainGrid width={50} defaultHeight={ true } style={ mainGridStyleSubscriber } display={displayKnowMore ? 'block' : 'none'}>
        <ListGrid cols={6} style={ gridListStyle }>
          <div>
            <Input type="text" placeholder="Email Anda" fontSize={1} textAlign="center" width={ 100 }/>
            <Button width={100} color="primary">Langganan</Button>
          </div>
        </ListGrid>
      </MainGrid>
    </div>
    </div>
  )
  }
}

export default Header;
